* {
    margin: 0;
}

:root {
    ---bg-color: rgb(153, 0, 255);
    --text-color: rgb(238, 249, 253);
}

body {
    background: var(---bg-color);
    height: 100vh;
    font-family: monospace;
}

button {
    cursor: pointer;
    border: 0;
}

.App {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.Home {
    padding: 2rem;
    background-color: var(--text-color);
    gap: 1rem;
    display: grid;
    width: 20rem;
    box-shadow: 0px 0px 20px var(--text-color);
}

.content {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.password input {
    width: 100%;
    border-radius: 6px;
    border: 1px solid black;
    outline: none;
}

.password input:focus {
    border: 2px solid var(---bg-color);
}

.password button {
    background-color: var(---bg-color);
    color: var(--text-color);
    width: 40%;
    padding: 8px 5px;
    font-weight: bold;
    border-radius: 6px;
}

.password button:hover {
    background-color: rgb(97, 13, 154);
}

.generate button {
    width: 100%;
    height: 2rem;
    margin-top: 1rem;
    background-color: var(---bg-color);
    color: var(--text-color);
    font-weight: bold;
    letter-spacing: 0.2rem;
}

.generate button:hover {
    background-color: rgb(97, 13, 154);
}

.content input[type=checkbox] {
    width: 15px;
    height: 15px;
    accent-color: var(---bg-color);
    cursor: pointer;
}

.content input[type=number] {
    width: 3rem;
    border: 1px solid black;
    border-radius: 3px;
    outline: none;
}

.content input[type=number]:focus {
    border: 2px solid var(---bg-color);
}
@media (max-width:385px) {
    .Home{
        padding: 1.1rem;
        width: 17rem;
    }
    .password{
        display: contents;
    }
    .password input{
        height: 1.5rem;
        padding: 0.4rem;
        width: 16rem;
    }
    .password button{
        width: 100%;
    }
}